<template>
  <div class="join">
    <Header/>
    <div class="joinWrap">
      <div class="main">
        <div class="logo">
          <img src="@/assets/imgs/join_logo.png" alt="">
        </div>
        <div class="regContainer">
          <div class="title">Join our Community</div>
          <div class="text">to receive the latest news and updates from the project!</div>
          <div class="form">
            <el-form ref="form" :model="form">
              <el-input v-model="form.email" placeholder="Enter your email"/>
              <el-input v-model="form.name" placeholder="Enter your name"/>
            </el-form>
          </div>
          <div class="consider">I consider myself a...</div>
          <div class="gender">
            <div class="item">
              <div class="img">
                <img src="@/assets/imgs/join_boy.png" alt="" style="width: 112px;">
              </div>
              <div class="boy">Boy</div>
              <div class="select" @click="handleSelect(0)">
                <img v-show="!form.sex" src="@/assets/imgs/icon_checked.png" alt="">
              </div>
            </div>
            <div class="item">
              <div class="img">
                <img src="@/assets/imgs/join_girl.png" alt="" style="width: 117px;">
              </div>
              <div class="boy">Girl</div>
              <div class="select" @click="handleSelect(1)">
                <img v-show="form.sex" src="@/assets/imgs/icon_checked.png" alt="">
              </div>
            </div>
          </div>
          <div class="info">
            <p>By clicking JOIN you agree to your personal data being stored and used to</p>
            <p>receive more information via email about our products/services,</p>
            <p>events, news and offers.</p>
          </div>
          <div class="joinBtn" @click="handleSubmit">JOIN</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "@/components/header";
import { submitEmail } from '@/request/api'

export default {
  name: "Join",
  components: { Header },
  data() {
    return {
      form: {
        email: '',
        name: '',
        sex: 0
      }
    }
  },
  methods: {
    handleSelect(val) {
      this.form.sex = val
    },
    handleSubmit() {
      if (this.form.email === '') {
        this.$message.error('Enter your email')
      } else if (this.form.name === '') {
        this.$message.error('Enter your name')
      } else {
        submitEmail(this.form).then(res => {
          if (res.success) {
            this.$message.success('Operation is successful')
          } else {
            this.$message.error(res.msg)
          }
        }).catch(() => {})
      }
    }
  }
}
</script>

<style lang="less" scoped>
.joinWrap{
  height: calc(100vh - 70px);
  margin-top: 70px;
  background: url("~@/assets/imgs/join_bg.png") no-repeat;
  background-size: 100% 100%;
  .main{
    width: 600px;
    margin: 0 auto;
    padding-top: 48px;
    .logo{
      width: 388px;
      margin: 0 auto 16px auto;
      img{
        width: 100%;
      }
    }
    .regContainer{
      padding: 0 42px;
      color: #fff;
      text-align: center;
      border: 2px solid #0aaa9a;
      height: 650px;
      border-radius: 20px;
      background: linear-gradient(180deg, #0B1A20 0%, #0B1A20 0%, #131D23 100%, #131D23 100%);
      box-shadow: 0 3px 15px 8px rgba(0, 0, 0, 0.3),inset 0 0 20px 3px rgba(0, 255, 183, 0.36);
      .title{
        font-family: Montserrat-Bold;
        font-size: 42px;
        font-weight: bold;
        background-image: -webkit-linear-gradient(top,#0bedad,#0ba191);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-top: 32px;
      }
      .text{
        font-family: Montserrat-Regular;
        font-size: 13px;
        margin-bottom: 26px;
      }
      .form{
        .el-input:first-child{
          margin-bottom: 14px;
        }
        /deep/ .el-input__inner{
          height: 50px;
          border-radius: 16px;
          background: linear-gradient(180deg, rgba(195,195,195,0.34) -1%, rgba(195,195,195,0.34) -1%, rgba(195,195,195,0.08) 100%, rgba(195,195,195,0.08) 100%);
          box-sizing: border-box;
          border: 2px solid #D8D8D8;
          backdrop-filter: blur(31px);
          box-shadow: inset 0 3px 20px 0 #FFFFFF;
          padding-left: 20px;
          font-family: Montserrat-Regular;
          font-size: 14px;
          color: #fff;
        }
      }
      .consider{
        font-family: Montserrat-Bold;
        font-size: 18px;
        font-weight: bold;
        text-transform: capitalize;
        color: #C3C3C3;
        margin: 24px 0 20px 0;
      }
      .gender{
        display: flex;
        justify-content: space-between;
        .item{
          width: 249px;
          height: 146px;
          border-radius: 16px;
          background: linear-gradient(180deg, rgba(195,195,195,0.34) -1%, rgba(195,195,195,0.34) -1%, rgba(195,195,195,0.08) 100%, rgba(195,195,195,0.08) 100%);
          box-sizing: border-box;
          border: 2px solid #D8D8D8;
          backdrop-filter: blur(31px);
          box-shadow: inset 0 3px 20px 0 #FFFFFF;
          position: relative;
          display: flex;
          .boy{
            font-family: Montserrat-Bold;
            font-size: 30px;
            font-weight: bold;
            margin: 40px 0 0 4px;
          }
          .select{
            position: absolute;
            width: 25px;
            height: 25px;
            background: #12191C;
            border: 2px solid #C3C3C3;
            box-shadow: inset 0px 4px 10px 0px #000000;
            right: 18px;
            top: 16px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            img{
              width: 15px;
            }
          }
        }
      }
      .info{
        font-family: Montserrat-Regular;
        font-size: 13px;
        line-height: 23px;
        padding-top: 14px;
        margin-bottom: 18px;
        p{
          margin-bottom: 0;
        }
      }
      .joinBtn{
        font-family: Montserrat-Bold;
        font-size: 20px;
        font-weight: bold;
        width: 229px;
        height: 37px;
        line-height: 37px;
        border-radius: 16px;
        background: linear-gradient(0deg, #818181 0%, #818181 0%, #FFFFFF 101%, #FFFFFF 101%);
        backdrop-filter: blur(31px);
        box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.3);
        margin: 0 auto;
        color: #0B1A20;
        cursor: pointer;
      }
    }
  }
}
</style>


